.backdrop {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0,0,0,0.5);
  }

.modalbg {
    position: absolute;
    z-index: 500;
    background-color: #f3fafa;
    width: 70%;
    border: none;
    border-radius: 4px;
    padding: 16px;
    left: 15%;
    top: 10%;
    box-sizing: border-box;
    transition: all 0.3s ease-out;
}

@media (min-width: 600px) {
    .modalbg {
        width: 520px;
        left: calc(50% - 250px);
    }
}