#form {
	/*width: 520px;
    height: auto;
    margin: 74px auto;
	background: #f3fafa;
	border-radius: 4px;
	color:#000;*/
	padding: 8px 32px;
}

#form h3 {
	margin-bottom: 2rem;
}

#form .scroller {
    padding: 2px 8px;
    margin: 0;
    height: 260px;
    min-width: 246px;
    overflow-x: hidden;
    overflow-y: auto;
}

#form .label {
	width: 25%;
	height: 38px;
	padding: 4px 8px;
	margin: 10px 0px;
    display: block;
    float: left;
    font-size: 18px;
}

#form input[type=text], input[type=number] {
	width: 64%;
	height: 38px;
	padding: 4px 8px;
	margin: 10px 0px;
	float: right;
	border: 1px solid #58810F;
	border-radius: 4px;
	resize: none;
}
#form input[type=button] {
	width: 120px;
    height: 64px;
    margin: 10px 46px;
    cursor: pointer;
    border: 1px solid green;
}

#form textarea {
	width: 75%;
	height: 100px;
	padding: 4px 8px;
	border: 1px solid #58810F;
	border-radius: 4px;
	resize: none;
}

#form input[type=submit] {
	position: absolute;
	/*margin:24px 4px;*/
	bottom: 21px;
	right: 18px;
	padding: 12px 32px;
	background-color: green;
	color: white;
	border: none;
	border-radius: 4px;
	text-decoration: none;
	cursor: pointer;
	float: right;
}
.btn-cancel{
    margin:5px 4px;
	padding: 12px 32px;
    background-color: red;
    color: white;
    border: none;
    border-radius: 4px;
}

@media screen and (max-width: 600px) {
	#form {
	  width: 99%;
	  padding: 18px 2px;
	}
	#form .scroller {
		height: 100%;
	}
	#form input[type=button] {
		margin: 10px 36px;
	}
}

@media screen and (max-width: 424px) {
	#form {
	  width: 99%;
	  height: auto;
	  padding: 18px 2px;
	}
}
