table{
    width: 100%;
    height:120px;
    padding: 8px;
}
tr:nth-child(odd){
    background-color: #f1f1c1;
}
tr:nth-child(even){
    background-color: #f6f6f6;
}
th, td{
    text-align: center;
    padding: 8px;
}
th{
    color: #006b6b;
    background-color: rgb(193, 241, 193);
}
button{
    margin: 4px;
}
.btn-green{
    padding: 8px 16px;
    background-color: green;
    color: white;
    border: none;
    border-radius: 4px;
}
.btn-red{
    padding: 8px 16px;
    background-color: red;
    color: white;
    border: none;
    border-radius: 4px;
}